













import Vue from 'vue';
import SvgIcon from '@/configurator/components/utils/SvgIcon.vue';
// @ts-ignore
import SvgEdit from '@/common/svgs/edit.svg';
// @ts-ignore
import SvgMagnifier from '@/common/svgs/magnifier.svg';
import {startConfiguring} from '@/configurator/embedding/helper';
import {UI_BUTTON} from '@/configurator/embedding/types';
import SelectionButton from '@/common/components/SelectionButton.vue';

export default Vue.extend({

  components: {
    SelectionButton,
    SvgIcon,
  },

  computed: {
    icons(): any {
      return {
        SvgEdit,
        SvgMagnifier,
      };
    },
    currentIcon(): any {
      return (this.$store.state.plannerUiState.selection === 'static') ? this.icons.SvgMagnifier : this.icons.SvgEdit;
    },
  },

  methods: {
    startConfiguring() {
      this.$embeddingCallbacks.onButtonClicked(UI_BUTTON.STARTCONFIGURE);
      if (this.$sdkConnector.hasPlanner) {
        this.$sdkConnector.loadSelectedPlanObject();
        return;
      }
      startConfiguring(this.$store, this.$embeddingCallbacks);
    },
  },
});
